.sidebar {
    width: 250px;
    background-color: #2c2c2c;
    color: #ffffff;
    padding: 20px;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
  }
  
  .sidebar h2 {
    color: #7289da;
    margin-bottom: 15px;
  }
  
  .sidebar-section {
    margin-bottom: 20px;
  }
  
  .file-drop-area {
    border: 2px dashed #7289da;
    border-radius: 5px;
    padding: 20px;
    text-align: center;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .file-drop-area:hover {
    background-color: rgba(114, 137, 218, 0.1);
  }
  
  .file-list {
    list-style-type: none;
    padding: 0;
  }
  
  .file-item {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    padding: 5px;
    background-color: #3a3a3a;
    border-radius: 5px;
  }
  
  .file-icon {
    width: 20px;
    height: 20px;
    margin-right: 10px;
  }
  
  .file-name {
    font-size: 14px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  .action-button {
    background-color: #7289da;
    color: white;
    border: none;
    padding: 10px 15px;
    margin: 5px 0;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .action-button:hover {
    background-color: #5a70b8;
  }