.chat-area {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    background-color: #2c2c2c;
    position: relative;
    overflow: hidden;
  }
  
  .chat-container {
    display: flex;
    flex-direction: column;
    height: 100%;
    z-index: 1;
  }
  
  .messages-container {
    flex-grow: 1;
    overflow-y: auto;
    padding: 20px;
  }
  
  .message {
    max-width: 70%;
    margin-bottom: 15px;
    padding: 10px 15px;
    border-radius: 20px;
    line-height: 1.4;
  }
  
  .message.user {
    align-self: flex-end;
    background-color: #7289da;
    color: white;
  }
  
  .message.ai, .message.team, .message.agent {
    align-self: flex-start;
    background-color: #4a4a4a;
    color: white;
  }
  
  .input-form {
    display: flex;
    padding: 20px;
    background-color: #2c2c2c;
  }
  
  .message-input {
    flex-grow: 1;
    padding: 10px;
    border: none;
    border-radius: 20px;
    background-color: #3a3a3a;
    color: white;
  }
  
  .send-button {
    margin-left: 10px;
    padding: 10px 20px;
    border: none;
    border-radius: 20px;
    background-color: #7289da;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .send-button:hover {
    background-color: #5a70b8;
  }
  
  #tsparticles {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 0;
  }
  
  .agent-selector {
    padding: 10px 20px;
    background-color: #3a3a3a;
  }
  
  .agent-selector label {
    margin-right: 10px;
  }
  
  .agent-selector select {
    padding: 5px;
    background-color: #4a4a4a;
    color: white;
    border: none;
    border-radius: 5px;
  }