body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #1e1e1e;
    color: #ffffff;
  }
  
  .app {
    display: flex;
    height: 100vh;
  }
  
  .main-content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  
  .top-nav {
    display: flex;
    justify-content: center;
    padding: 10px;
    background-color: #2c2c2c;
  }
  
  .nav-button {
    background-color: #4a4a4a;
    color: #ffffff;
    border: none;
    padding: 10px 20px;
    margin: 0 5px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .nav-button:hover {
    background-color: #5a5a5a;
  }
  
  .nav-button.active {
    background-color: #7289da;
  }
  
  .sidebar {
    width: 250px;
    background-color: #2c2c2c;
    padding: 20px;
    overflow-y: auto;
  }
  
  .sidebar h2 {
    color: #7289da;
    margin-bottom: 15px;
  }
  
  .sidebar-section {
    margin-bottom: 20px;
  }
  
  .file-drop-area {
    border: 2px dashed #7289da;
    border-radius: 5px;
    padding: 20px;
    text-align: center;
    cursor: pointer;
  }
  
  .file-list {
    list-style-type: none;
    padding: 0;
  }
  
  .file-list li {
    margin-bottom: 10px;
  }
  
  button {
    background-color: #7289da;
    color: white;
    border: none;
    padding: 10px 15px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #5a70b8;
  }
  
  input[type="text"], textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    border: 1px solid #4a4a4a;
    background-color: #3a3a3a;
    color: #ffffff;
  }
  
  input[type="text"]:focus, textarea:focus {
    outline: none;
    border-color: #7289da;
  }